html {
    text-align: center;
    alignment: center;
    justify-content: center;
    transition-property: vertical-align;

}

#inputDiv {
    justify-content: center;
    alignment: center;
}

h1 {
    font-size: 70px !important;
    background-color: #D2E2CF !important;
}


.tableFixHead {
    overflow: auto;
    height: 220px;
}

.tableFixHead thead th {
    position: sticky;
    top: 0;
    z-index: 1;
}

/* Just common table stuff. Really. */
table {
    border-collapse: collapse;
    width: 100%;
}

.tableBody{
    border-bottom: 1px solid black;
    border-top: 1px solid black;
}

th, td {
    padding: 4px 0px;
}

th {
    background: #D2E2CF;
}


.component {
    background-color: #D2E2CF;
}

.logout {
    background-color: #D2E2CF;
    font-size: 40px !important;
    margin: 0px 0px 0px 0px;
}

.logoutButton {
    position: absolute;
    right: 16px;

}

.setting {
    font-size: 40px !important;
}

.stopwatch {
    font-size: 100px !important;
}

#errormessage {
    color: red !important;
}

a.buttonLink:visited {
    text-decoration: none;
    color: white;
    font-weight: normal;
}

a.buttonLink:link {
    text-decoration: none;
    color: white;
    font-weight: normal;
}

a.normalLink:visited {
    text-decoration: none;
    color: blue;
    font-weight: normal;
}

a.normalLink:link {
    text-decoration: none;
    color: blue;
    font-weight: normal;
}


.App {
    text-align: center;
}


.App-logo {
    height: 40vmin;
    pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
    .App-logo {
        animation: App-logo-spin infinite 20s linear;
    }
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}
